:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram: linear gradient hsl(37, 97%, 70%) to hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --bg-toggle: hsl(230, 22%, 74%);
  --color-bg: hsl(0, 0%, 100%);
  --color-bg-top-pattern: hsl(225, 100%, 98%);
  --color-bg-card: hsl(227, 47%, 96%);
  --color-text: hsl(230, 17%, 14%);
  --color-text-desaturated: hsl(228, 12%, 44%);
}
:root body.dark {
  --bg-toggle: linear gradient hsl(210, 78%, 56%) to hsl(146, 68%, 55%);
  --color-bg: hsl(230, 17%, 14%);
  --color-bg-top-pattern: hsl(232, 19%, 15%);
  --color-bg-card: hsl(228, 28%, 20%);
  --color-text: hsl(0, 0%, 100%);
  --color-text-desaturated: hsl(228, 34%, 66%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--color-bg);
  color: var(--color-text);
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-size: 1.5rem;
}
@media (min-width: 59.375em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 59.375em) {
  .container {
    padding: 0;
  }
}

.card {
  padding: 1.25rem;
}

.toggle {
  border: none;
}
.toggle__button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--bg-toggle);
}

@media (min-width: 59.375em) {
  header {
    display: flex;
    justify-content: space-between;
  }
}